.pokemon-profile-container {
    margin: 10px 0 50px !important;
}

.pokemon-profile-name {
    text-transform: capitalize !important;
    display: flex !important;
    justify-content: center !important;
}

.pokemon-profile-boxart-container {
    margin: 5px 15px !important;
    justify-content: center !important;
}

.pokemon-profile-carousel-container {
    max-width: 450px !important;
    max-height: 450px !important;
    min-height: 350px !important;
    margin: 20px auto !important;
}

.pokemon-profile-carousel-navigation-icon-right {
    max-width: 30px !important;
    max-height: 30px !important;
}

.pokemon-profile-carousel-img {
    width: 300px !important;
    height: 300px !important;
    border: 5px solid black !important;
    border-radius: 50% !important;
    background-color: white !important;
}
.pokemon-profile-carousel-navigation-icon-left {
    transform: rotate(180deg) !important;
    max-width: 30px !important;
    max-height: 30px !important;
}

.pokemon-profile-carousel-img-container {
    display: flex !important;
    justify-content: center !important;
    vertical-align: 50% !important;
}

.pokemon-profile-boxart-wrapper {
    margin: auto 0 0 !important;
    display: flex !important;
    justify-content: center !important;
}

.pokemon-profile-boxart-overlay {
    position: absolute !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    background-color: black !important;
    opacity: 75% !important;
    overflow: hidden !important;
    width: 100% !important;
    height: 0 !important;
    transition: .5s ease !important;
}

.pokemon-profile-boxart-overlay-content {
    white-space: nowrap !important; 
    color: white !important;
    font-size: 20px;
    position: absolute !important;
    overflow: hidden !important;
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
    -ms-transform: translate(-50%, -50%) !important;
}

.pokemon-profile-boxart-overlay-title {
    text-transform: capitalize !important;
}
.pokemon-profile-boxart-container:hover .pokemon-profile-boxart-overlay {
    height: 100% !important;
    padding: 5% !important;
    cursor: pointer !important;
}

.pokemon-profile-boxart {
    display: block !important;
    width: 100% !important;
    height: auto !important;
}

.pokemon-profile-boxart:hover {
    cursor: pointer !important;
}